body {
  background: #ffffff;
  margin: 0;
  font-family: "Nunito", sans-serif;
  font-size: 13px !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
input::-webkit-input-placeholder {
  color: #878787;
}
input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #878787;
}
input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #878787;
}
input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #878787;
}
/* chrome */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
/* 火狐浏览器 */
input[type="number"] {
  -moz-appearance: textfield;
}
.style_mask__9zmSN {
  z-index: 99999999999 !important;
}
.bd-right,
.bd-right1,
.bd-bottom {
  position: relative;
}
.bd-right::before {
  content: "";
  width: 0.5px;
  background: #eee;
  position: absolute;
  opacity: 0.6;
  top: 50%;
  bottom: 0;
  right: 0;
  transform-origin: 100% 50%;
  height: 55%;
  transform: translateY(-50%);
}
.bd-right1::before {
  content: "";
  width: 0.5px;
  background: #eee;
  position: absolute;
  opacity: 0.6;
  top: 50%;
  bottom: 0;
  right: 0;
  transform-origin: 100% 50%;
  height: 100%;
  transform: translateY(-50%);
}

.bd-bottom::before {
  content: "";
  height: 0.5px;
  opacity: 0.6;
  background: #eee;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  transform-origin: 50% 100%;
}

